<template><section><h2>Circle 进度环</h2>
<p>用于展示操作进度百分比以及数据占比等</p>
<h3>基础用法</h3>
<coding title="进度环基础配置" note="进度环的进度由percent字段控制，进度环颜色由strokeColor控制" class="circle-first-box" :code="baseCode">
  <RuCircle :percent="50" :style="{fontSize: '20px'}">50%</RuCircle>
  <RuCircle :percent="100" strokeColor="#27c24c"><RuIcon type="ios-checkmark-empty" size="55px" color="#27c24c"></RuIcon></RuCircle>
  <RuCircle :percent="30" strokeColor="#ef5050"><RuIcon type="ios-close-empty" size="55px" color="#ef5050"></RuIcon></RuCircle>
</coding>
<coding title="常见场景" note="进度环常见用来与其他组件配合实现交互" :code="demoCode">
  <RuCircle :percent="percent" :strokeColor="color">
    <span v-if="percent !== 100" style="font-size: 20px">{{percent}}%</span>
    <RuIcon type="ios-checkmark-empty" size="55px" color="#27c24c" v-else=""></RuIcon>
  </RuCircle>
  <RuButton type="primary" :style="{margin: '10px'}" @click="add">增</RuButton>
  <RuButton type="primary" @click="reduce">减</RuButton>
</coding>
<h3>自定义内容</h3>
<coding title="自定义进度环内容" note="通过自定义进度环内容，可以展示统计结果等数据" :code="slotCode">
  <RuCircle :percent="18.7" :size="300" strokeColor="#20a3f7" :backWidth="2" :strokeWidth="3" linecap="square">
    <div>
      <h2>1,390,080,000</h2>
      <p>2017中国人口</p>
      <p>世界占比18.7%</p>
    </div>
  </RuCircle>
</coding>
<h3>Attributes</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>percent</td>
<td>进度值</td>
<td>Number</td>
<td>-</td>
<td>0</td>
</tr>
<tr>
<td>size</td>
<td>进度环的宽高</td>
<td>Number</td>
<td>-</td>
<td>120</td>
</tr>
<tr>
<td>linecap</td>
<td>进度环头尾的形状</td>
<td>String</td>
<td>square/round</td>
<td>round</td>
</tr>
<tr>
<td>strokeWidth</td>
<td>进度环的线条宽度</td>
<td>Number</td>
<td>-</td>
<td>6</td>
</tr>
<tr>
<td>strokeColor</td>
<td>进度条颜色</td>
<td>String</td>
<td>-</td>
<td>#fe6f3d</td>
</tr>
<tr>
<td>backWidth</td>
<td>进度环背景宽度</td>
<td>number</td>
<td>-</td>
<td>5</td>
</tr>
<tr>
<td>backColor</td>
<td>进度环背景颜色</td>
<td>String</td>
<td>-</td>
<td>#e4e4e4</td>
</tr>
</tbody>
</table>

</section></template>
<style lang="scss" scoped>
.circle-first-box {
  .ru-circle {
    display: inline-block;
    margin-right: 20px;
  }
}
</style>
<script>
let baseCode = `
  <RuCircle :percent="50" :style="{fontSize: '20px'}">50%</RuCircle>
  <RuCircle 
    :percent="100" 
    strokeColor="#27c24c"
  >
    <RuIcon type="ios-checkmark-empty" size="55px" color="#27c24c"></RuIcon>
  </RuCircle>
  <RuCircle 
    :percent="30" 
    strokeColor="#ef5050"
  >
    <RuIcon type="ios-close-empty" size="55px" color="#ef5050"></RuIcon>
  </RuCircle>
`

let demoCode = `
  <RuCircle :percent="percent" :strokeColor="color">
    <span v-if="percent !== 100" style="font-size: 20px">{{percent}}%</span>
    <RuIcon type="ios-checkmark-empty" size="55px" color="#27c24c" v-else></RuIcon>
  </RuCircle>
  <RuButton type="primary" :style="{margin: '10px'}" @click="add">增</RuButton>
  <RuButton type="primary" @click="reduce">减</RuButton>

  export default {
  data() {
    return {
      percent: 0
    }
  },
  computed: {
    color() {
      return this.percent === 100 ? '#27c24c' : '#fe6f3d'
    }
  },
  methods: {
    add() {
      if (this.percent === 100) return
      this.percent += 10
    },
    reduce() {
      if (this.percent === 0) return
      this.percent -= 10
    }
  }
}
`
let slotCode = `
  <RuCircle :percent="18.7" :size="300" strokeColor="#20a3f7" :backWidth="2" :strokeWidth="3">
    <div class="circle-demo-content">
      <h2>1,390,080,000</h2>
      <p>2017中国人口</p>
      <p>世界占比18.7%</p>
    </div>
  </RuCircle>
`

export default {
  data() {
    return {
      baseCode: baseCode,
      percent: 0,
      demoCode: demoCode,
      slotCode: slotCode
    }
  },
  computed: {
    color() {
      return this.percent === 100 ? '#27c24c' : '#fe6f3d'
    }
  },
  methods: {
    add() {
      if (this.percent === 100) return
      this.percent += 10
    },
    reduce() {
      if (this.percent === 0) return
      this.percent -= 10
    }
  }
}
</script>


